@import url('https://fonts.googleapis.com/css2?family=PT+Mono&display=swap');
body {
  margin: 0;
  font-family: 'PT Mono', monospace,'Helvetica Neue',
    sans-serif;
  background-color: #676767;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.bgimgbox{
  display: grid;
  position: absolute;
  top: 0;
}
.imgbox{
  display: grid;
  padding: 0;
  margin: 1rem;
}
.imgbox span{
  border: 1px black solid;
  display: inline;
  text-align: center;
  font-size: .7rem;
  background-color: #A2A2A2;
}
.imgbox img{
  width: 5rem;
}
.mainbx{
  position: relative;
  top: 0;
  width: 50%;
  margin: 5rem auto;
  border: 2px black solid;
  background-color: #A2A2A2;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.bx1{
  display: flex;
  width: 100%;
}
.hd{
  width: 90%;
  font-size: 1rem;
  background-color: #012e81;
  color: #fff;
  border: 2px solid black;
  margin: .2rem 0 .2rem 0.2rem;
  padding: 0.2rem;
}
.xbox{
  background-color: #A2A2A2;
  border: 2px solid #4a4a4a;
  margin: .2rem;
  padding: 0 0.2rem;
  color: #676767;
  font-size: 1.2rem;
}
.bx2{
  width: 85%;
  margin: auto;
  padding: 0;
  text-align: center;
  display: grid;
}
.btns{
  display: flex;
  margin: auto;
}
.btns button{
  border: none;
  background-color: transparent;
  cursor: pointer;
}
button img{
  width: 2.5rem;
  border-radius: 20px;
}
.bx3{
  background-color: #fff;
  margin: .5rem;
  padding: 0;
  border: 2px solid black;

}
.bx3>.inputwrap{
 width: 15rem;
 margin: auto;
 padding: 2rem;
 display: flex;
}
.addbtn{
  background-color: #a2a2a2;
  border: 2px solid black;
}
.bx3 input{
  border-bottom: 1px solid black;
  background-color: #aeaeae;
  margin: 0 auto;
  width: 12rem;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid black;

}
.footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2rem;
  background-color: #a2a2a2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer>span{
  font-size: .7rem;
  font-family: Arial, Helvetica, sans-serif;
}
@media screen and (max-width: 789px) {
  .imgbox img{
    width: 3rem;
  }
  .imgbox span{
    border: 1px black solid;
    display: inline;
    text-align: center;
    font-size: .5rem;
    background-color: #A2A2A2;
  }
  .bx3>.inputwrap{
    width: 6rem;
    margin: auto;
    padding: 1rem;
    display: flex;
    justify-content: center;
   }

   button img{
    width: 1.5rem;
    border-radius: 20px;
  }
}
@media screen and (max-width: 450px){

  .bx3 input{
    border-bottom: 1px solid black;
    margin: 0 auto;
    width: 5rem;
  }
  .ullist{
    word-break:break-all;
  }
}